import React, {useContext, useEffect} from "react";
import {INotice, NoticeType, RoomNotifierContext} from "./notifications/GameNotifier";
import {RoomContext} from "../../context/room-context";
import {IRoomNotice, MessageTypes, RoomNoticeTypes} from "../../werewolf-consts/colyseus/MessageTypes";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import {ISetupError, SetupErrorTypes} from "../../werewolf-consts/colyseus/RoomErrors";
import {RoleGuideEnum} from "../../language/Vietnamese/RoleGuide_vi";
import {TOptions} from "i18next";
import {WereWolfEvent} from "../../werewolf-consts/colyseus/state/WereWolfLog";
import {GameNoticeEnum} from "../../language/Vietnamese/GameNotice_vi";
import {mapSchema2Object} from "../../werewolf-consts/colyseus/Utils";
import {EventData} from "../../werewolf-consts/definitions/EventData";
import {getPlayersName} from "../../werewolf-consts/colyseus/stateUtils";

function RoomNoticeListener() {
    const notifier = useContext(RoomNotifierContext);
    const room = useContext(RoomContext).room;
    const players = room.state.players;
    const adminId = room.state.roomSetting.roomAdmin;
    const adminName = players[adminId].name;
    const {t} = useTranslation([I18Namspace.GameNotice, I18Namspace.RoleGuide]);

    function getSetupErrOption(arr: any[], errType: SetupErrorTypes): TOptions | undefined {
        switch (errType) {
            case SetupErrorTypes.ROLE_REQUIRE_NOT_FIT:
                return {
                    roleName: t(`${I18Namspace.RoleGuide}:${RoleGuideEnum.name}.${arr[0]}`),
                    requireRoleName: t(`${I18Namspace.RoleGuide}:${RoleGuideEnum.name}.${arr[1]}`)
                };
            case SetupErrorTypes.ROLE_MAX_NOT_FIT:
                return {maxCount: arr[1], roleName: t(`${I18Namspace.RoleGuide}:${RoleGuideEnum.name}.${arr[0]}`)};

            default:
                return undefined;
        }
    }

    useEffect(() => {
        room.onMessage<IRoomNotice>(MessageTypes.ROOM_NOTICE, function (notice) {
            let content = "";
            switch (notice.type) {
                case RoomNoticeTypes.ADMIN_CHANGE_SETUP:
                    if (notice.data === true) {
                        content = t(`${I18Namspace.GameNotice}:${RoomNoticeTypes.ADMIN_CHANGE_SETUP}`, {
                            adminName: adminName
                        });
                        notifier.showNotice({time: 3, content: content, type: NoticeType.info} as INotice);
                        return;
                    } else {
                        let errData = notice.data as ISetupError;
                        content = t(
                            `${I18Namspace.GameNotice}:${errData.type}`,
                            getSetupErrOption(errData.data, errData.type)
                        );
                        notifier.showNotice({time: 3, content: content, type: NoticeType.error} as INotice);
                    }
                    break;
                case RoomNoticeTypes.ADMIN_KICK_PLAYER:
                    notifier.showNotice({
                        time: 3,
                        content: t(
                            `${I18Namspace.GameNotice}:${GameNoticeEnum.playersChange}.${RoomNoticeTypes.ADMIN_KICK_PLAYER}`,
                            {
                                name: getPlayersName(room.state, notice.data, true)
                            }
                        ),
                        type: NoticeType.error
                    });
                    break;
                case RoomNoticeTypes.PLAYER_JOIN_ROOM:
                    notifier.showNotice({
                        time: 3,
                        type: NoticeType.success,
                        content: t(
                            `${I18Namspace.GameNotice}:${GameNoticeEnum.playersChange}.${RoomNoticeTypes.PLAYER_JOIN_ROOM}`,
                            {
                                name: getPlayersName(room.state, notice.data, true)
                            }
                        )
                    });
                    break;
                case RoomNoticeTypes.PLAYER_LEAVE_ROOM:
                    notifier.showNotice({
                        time: 3,
                        type: NoticeType.warning,
                        content: t(
                            `${I18Namspace.GameNotice}:${GameNoticeEnum.playersChange}.${RoomNoticeTypes.PLAYER_LEAVE_ROOM}`,
                            {
                                name: getPlayersName(room.state, notice.data, true)
                            }
                        )
                    });
                    break;
                case RoomNoticeTypes.ROOM_SETTING_UPDATED:
                    notifier.showNotice({
                        time: 3,
                        type: NoticeType.success,
                        content: t(`${I18Namspace.GameNotice}:${GameNoticeEnum.roomSettingUpdated}`, {
                            name: getPlayersName(room.state, notice.data, true)
                        })
                    });
                    break;
            }
        });
        room.onMessage<WereWolfEvent>(MessageTypes.ACTION_PUSH, function (notice) {
            let eventData = mapSchema2Object<EventData>(notice.data);
            let actor = getPlayersName(room.state, [notice.from || ""], true)[0];
            let targets = getPlayersName(room.state, eventData.targets, true);
            notifier.showNotice({
                type: NoticeType.info,
                content: t(`${I18Namspace.GameNotice}:${GameNoticeEnum.actionPush}.${notice.eventName}`, {
                    name: actor,
                    target: targets
                }),
                time: 3
            });
        });
        room.onMessage(MessageTypes.CHAT_REFUSED, function (notice) {
            notifier.showNotice({
                type: NoticeType.warning,
                content: t(`${I18Namspace.GameNotice}:${GameNoticeEnum.chatRefuse}`),
                time: 3
            });
        });
    }, [room]);
    return <div />;
}

export default RoomNoticeListener;
