import React, {useContext, useEffect, useState} from "react";
import {SkillNames} from "../../../../../werewolf-consts/enums/SkillNames";
import {StateContext} from "../../../../../context/StateContext";
import {RoomContext} from "../../../../../context/room-context";

function useAttackerMap() {
    const [attackerMap, setAttackerMap] = useState<Map<string, string[]>>(new Map<string, string[]>([]));
    let stateContext = useContext(StateContext);
    let roomContext = useContext(RoomContext);
    let targets = [...Object.keys(roomContext.room.state.players)];

    function updateAttackerMap() {
        setAttackerMap((attackerMap) => {
            let newMap = new Map([...attackerMap]);

            for (const target of newMap.keys()) {
                // target=[]
                newMap.set(target, []);
            }
            for (const action of stateContext.currentStage.actions) {
                if (action.skill === SkillNames.POINT) {
                    for (const targetKey of action.targets) {
                        if (newMap.get(targetKey) !== undefined) {
                            newMap.get(targetKey)?.push(action.from);
                        } else newMap.set(targetKey, [action.from]);
                    }
                }
            }
            return newMap;
        });
    }

    useEffect(() => {
        updateAttackerMap();
    }, [stateContext.currentStage.actions]);
    useEffect(() => {
        initAttackerMap();
    }, [stateContext.currentStage.stageName]);

    function initAttackerMap() {
        if (targets)
            setAttackerMap((attackerMap) => {
                for (const target of targets) {
                    attackerMap.set(target, []);
                }
                return attackerMap;
            });
    }

    return attackerMap;
}

export function besidePlayers(total: number, seat: number) {
    let left = seat - 1;
    let right = seat + 1;
    if (left < 1) left = total;
    if (right > total) right = 1;
    return {
        left: left,
        right: right
    };
}

export default useAttackerMap;
