import React, {ReactElement, useContext, useEffect, useState} from "react";
import GameLayout from "./GameLayout";

interface RouterProps {
    paths: string[];
    children: ReactElement[];
    initShow?: string;
}

function GameRouter(props: RouterProps) {
    const fatherRoute = useContext(GameRouterContext);
    const fatherPath = fatherRoute.fullPath || "";
    const [path, setPath] = useState<string>();
    const [childPath, setChildPath] = useState<string>();
    const [params, setParams] = useState<(string | undefined)[]>();
    const [childParams, setChildParams] = useState<(string | undefined)[]>();

    function changeRoute(path: string, params: (string | undefined)[]) {
        let subPaths = path.split("/");
        if (props.paths.includes(subPaths[0])) {
            setPath(subPaths[0]);
            if (subPaths.length > 1) {
                setChildPath(subPaths.slice(1).toString().replace(",", "/"));
                setChildParams(params);
            } else {
                setParams(params);
            }
        } else {
            if (fatherRoute.routeTo) fatherRoute.routeTo(path, params);
            return;
        }
    }

    useEffect(() => {
        if (fatherRoute.currentPath != null) {
            let subPaths = fatherRoute.currentPath.split("/");
            if (props.paths.includes(subPaths[0])) {
                setPath(subPaths[0]);
                if (subPaths.length > 1) {
                    setChildPath(subPaths.slice(1).toString().replace(",", "/"));
                    setChildParams(fatherRoute.childParams);
                } else {
                    setParams(fatherRoute.childParams || []);
                }
            }
        } else {
            setPath(props.initShow || props.paths[0]);
        }
    }, [fatherRoute.currentPath]);

    return (
        <>
            {props.children.map((el, index) => {
                return (
                    <GameLayout.Condition con={props.paths[index] === path} key={fatherPath + props.paths[index]}>
                        <GameRouterContext.Provider
                            value={{
                                fullPath: `${fatherPath}/${path}`,
                                currentPath: childPath,
                                params: params,
                                childParams: childParams,
                                routeTo: changeRoute
                            }}
                        >
                            {el}
                        </GameRouterContext.Provider>
                    </GameLayout.Condition>
                );
            })}
        </>
    );
}

interface IRouterContext {
    routeTo?: (path: string, params: (string | undefined)[]) => void;
    fullPath?: string;
    currentPath?: string;
    params?: (string | undefined)[];
    childParams?: (string | undefined)[];
}

export const GameRouterContext = React.createContext<IRouterContext>({});

export default GameRouter;
