import React, {useContext, useEffect, useRef, useState} from "react";
import {RoomContext} from "../../context/room-context";
import {Room} from "colyseus.js";
import {State} from "../../werewolf-consts/colyseus/State";
import {CLIENT, DEV, FacebookPlayer} from "../../context/facebook-context";
import {ROLE_NAMES, Roles, StageNames} from "../../werewolf-consts/enums";
import StageRouter from "./StageRouter";
import {IState, stateSchema2IState} from "../../colyseus/IState";
import {StateContext} from "../../context/StateContext";
import {AdminActionTypes, MessageTypes} from "../../werewolf-consts/colyseus/MessageTypes";
import GameNotifier from "./notifications/GameNotifier";
import GameInfoView from "./game-info/GameInfoView";
import ChatButton from "./game-body/chat/ChatButton";
import RoomNoticeListener from "./RoomNoticeListener";
import {NewStateContext} from "../../context/NewStateContext";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import GameLayout from "../shared/game-advance-components/GameLayout";
import GameRouter from "../shared/game-advance-components/GameRouter";
import {FakeID} from "../../werewolf-consts/definitions/RoleConsts";
import {SizeContext} from "../../context/SizeContext";
import GameGuiManager from "./GameGuiManager";

TestScreen.propTypes = {};
const testStage = StageNames.WOLF_PLUS;
const testRole = Roles.WOLF_PLUS;

function TestScreen() {
    let room = useRef<Room<State>>({} as Room<State>);
    const [roomState, setRoomState] = useState<IState>();
    const [gameState, setGameState] = useState<State>(new State());
    const {window} = useContext(SizeContext);

    function simpleNextStage() {
        room.current.send(MessageTypes.ADMIN_ACTION, {type: AdminActionTypes.SKIP_STAGE, data: ""});
    }

    function jumToNextTestStage() {
        room.current.send(MessageTypes.ADMIN_ACTION, {type: AdminActionTypes.JUMP_TO_STAGE, data: testStage});
    }

    async function resetGame() {
        await room.current.send(MessageTypes.ADMIN_ACTION, {type: AdminActionTypes.RESET_TEST_GAME, data: testStage});
    }

    useEffect(() => {
        let fakeUser: FacebookPlayer = {
            playerId: FakeID[testRole],
            playerName: ROLE_NAMES[testRole],
            playerPic: "",
            signature: ""
        };
        CLIENT.joinOrCreate<State>("my_room", {...fakeUser, villageId: fakeUser.playerId})
            .then((r) => {
                room.current = r;
                resetGame().then(() => {
                    room.current.onStateChange((state) => {
                        setGameState({...state} as State);
                        setRoomState(stateSchema2IState(state));
                        // room.current.state.currentStage.activePlayers=array2ArraySchema<string>([]);
                    });
                });
            })
            .catch((e) => {
                if (DEV) console.log(e);
            });
        return function () {};
    }, []);
    return (
        <GameGuiManager>
            <GameNotifier>
                <GameLayout.ColExpanded>
                    <GameDiv position={"absolute"}>
                        <button onClick={simpleNextStage}>N</button>
                        <button onClick={jumToNextTestStage}>J</button>
                        <button onClick={() => resetGame()}>R</button>
                    </GameDiv>
                    <RoomContext.Provider value={{room: room.current, myId: FakeID[testRole]}}>
                        {roomState && (
                            <StateContext.Provider value={roomState}>
                                <NewStateContext.Provider value={gameState}>
                                    <RoomNoticeListener />
                                    {/*<button onClick={nextStage}>NextStage</button>*/}
                                    <GameLayout.RowExpanded reverse={true}>
                                        <GameRouter paths={["StageRouter", "GameInfoView"]}>
                                            <StageRouter />
                                            <GameInfoView />
                                        </GameRouter>
                                        <ChatButton initShow={true} />
                                    </GameLayout.RowExpanded>
                                </NewStateContext.Provider>
                            </StateContext.Provider>
                        )}
                    </RoomContext.Provider>
                </GameLayout.ColExpanded>
            </GameNotifier>
        </GameGuiManager>
    );
}

export default TestScreen;
