import React, {useContext, useEffect, useState} from "react";
import PropTypes from "prop-types";
import {COLOR, ColorEnum} from "../../shared/styled/ColorsPalette";
import {v4 as uuidv4} from "uuid";
import XButton from "../../shared/game-basic-components/XButton";
import {GameText} from "../../shared/game-basic-components/GameText";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../context/SizeContext";

GameNotifier.propTypes = {
    //
    children: PropTypes.element
};

export enum NoticeType {
    error,
    warning,
    success,
    info
}

export interface INotice {
    content: string;
    time: number;
    uuid?: string;
    type?: NoticeType;
}

interface IGameAlert {
    showNotice(alert: INotice);
}

export const RoomNotifierContext = React.createContext<IGameAlert>({
    showNotice(alert: INotice) {}
});

function GameNotifier(props) {
    const [notices, setNotices] = useState<INotice[]>([]);
    const {window, basic} = useContext(SizeContext);

    useEffect(() => {
        if (notices.length > 0) {
            let i = setTimeout(() => {
                setNotices((list) => {
                    const notices = [...list];
                    for (const notice of notices) {
                        notice.time -= 1;
                    }
                    return notices.filter((a) => {
                        return a.time > 0;
                    });
                });
            }, 1000);
            return function clean() {
                clearTimeout(i);
            };
        }
    }, [notices]);

    function showNotice(alert: INotice) {
        if (alert.content == null) return;
        setNotices((list) => {
            let newList = [...list].slice(0, 4);
            return [{...alert, uuid: uuidv4(), type: alert.type || NoticeType.error}, ...newList];
        });
    }

    function close(alertID: string | undefined) {
        let list = notices.filter((a) => {
            return a.uuid !== alertID;
        });
        setNotices(list);
    }

    return (
        <RoomNotifierContext.Provider value={{showNotice: showNotice}}>
            <GameLayout.Col
                $zIndex={11}
                position={"fixed"}
                ml={basic.mobileView ? (window.width - basic.gameW * 0.8) / 2 : 0}
                $alignSelf={"flex-start"}
                width={basic.mobileView ? basic.gameW * 0.8 : basic.chatW}
                $alignItems={basic.mobileView ? "center" : "flex-start"}
                $justifyContent="flex-start"
            >
                {notices.map((a, i) => {
                    return (
                        <GameLayout.RowExpanded
                            key={`alert_${i}`}
                            $justifyContent="center"
                            $backgroundColor={COLOR[`${ColorEnum.noticeBackground}${a.type}`]}
                            round={".5rem"}
                            mt={".3rem"}
                            p={".5rem"}
                        >
                            <GameLayout.ColExpanded $justifyContent="center" $alignContent="flex-start">
                                <GameText
                                    color={COLOR[`${ColorEnum.noticeText}${a.type}`]}
                                    p={".1rem"}
                                    $fWeight={"bolder"}
                                >
                                    {a.content}
                                </GameText>
                            </GameLayout.ColExpanded>
                            <XButton
                                color={COLOR[`${ColorEnum.noticeText}${a.type}`]}
                                onClick={() => {
                                    close(a.uuid);
                                }}
                            />
                        </GameLayout.RowExpanded>
                    );
                })}
            </GameLayout.Col>

            {props.children}
        </RoomNotifierContext.Provider>
    );
}

export default GameNotifier;
