import React, {ReactElement, useEffect, useRef, useState} from "react";
import {GameDiv, IBasicProps} from "../game-basic-components/GameDiv";
import {POSITION} from "../styled/game-layout/StackLayout";
import {IPositionProps} from "../styled/BasicProps";

function getAbsolutePos(pos: POSITION, W: number, H: number) {
    let left, right, bottom, ml, mb;
    switch (pos) {
        case POSITION.BOTTOM_RIGHT:
            left = "100%";
            break;
        case POSITION.BOTTOM_LEFT:
            right = "100%";
            break;
        case POSITION.TOP_LEFT:
            right = "100%";
            bottom = "100%";
            break;
        case POSITION.TOP_RIGHT:
            left = "100%";
            bottom = "100%";
            break;
        case POSITION.TOP:
            left = "50%";
            bottom = "100%";
            ml = `-${W / 2}px`;
            break;
        case POSITION.BOTTOM:
            left = "50%";
            ml = `-${W / 2}px`;
            break;
        case POSITION.LEFT:
            bottom = "50%";
            right = "100%";
            mb = `-${H / 2}px`;
            break;
        case POSITION.RIGHT:
            bottom = "50%";
            left = "100%";
            mb = `-${H / 2}px`;
            break;
    }
    return {
        left: left,
        right: right,
        bottom: bottom,
        ml: ml,
        mb: mb
    };
}

interface IPOS {
    left: string;
    right: string;
    bottom: string;
    ml: string;
    mb: string;
}

function GameHoverPopup(props: IProps) {
    const [show, setShow] = useState<boolean>(false);
    const customProps = {...props};
    delete customProps["children"];
    delete customProps["popup"];
    delete customProps["pos"];
    const child = useRef<HTMLDivElement>(null);
    const [POS, setPOS] = useState<IPOS>({} as IPOS);

    useEffect(() => {
        const {left, right, bottom, ml, mb} = getAbsolutePos(
            props.pos != null ? props.pos : POSITION.RIGHT,
            child.current?.clientWidth || 0,
            child.current?.clientHeight || 0
        );
        setPOS({
            left: left,
            right: right,
            bottom: bottom,
            ml: ml,
            mb: mb
        });
    }, [props.popup]);

    return (
        <GameDiv
            onMouseEnter={() => setShow(true)}
            onMouseLeave={() => setShow(false)}
            position={"relative"}
            display={"inline-block"}
        >
            {props.children}
            <div
                ref={child}
                style={{
                    position: "absolute",
                    zIndex: 11,
                    visibility: show ? "visible" : "hidden",
                    left: POS.left,
                    right: POS.right,
                    marginLeft: POS.ml,
                    marginBottom: POS.mb,
                    bottom: POS.bottom,
                    padding: ".2rem"
                }}
            >
                <GameDiv {...customProps}>{props.popup}</GameDiv>
            </div>
        </GameDiv>
    );
}

interface IProps extends IBasicProps, IPositionProps {
    children: ReactElement;
    popup: ReactElement;
}

export default GameHoverPopup;
