import React, {useContext, useEffect, useState} from "react";
import {StateContext} from "../../../../../context/StateContext";
import {SkillNames} from "../../../../../werewolf-consts/enums/SkillNames";
import {StageNames} from "../../../../../werewolf-consts/enums/StageNames";
import {RoomContext} from "../../../../../context/room-context";
import {useTranslation} from "react-i18next";
import {SkillDisabledReasonEnum} from "../../../../../language/Vietnamese/SkillDisabledReason_vi";
import {MAX_PLAYER_IN_GAME, MIN_PLAYER_IN_GAME} from "../../../../../werewolf-consts/definitions/PresetSetups";
import {I18Namspace} from "../../../../../language/i18n";
import {countRoleEvent} from "../../../../../werewolf-consts/colyseus/stateUtils";
import {EventNames} from "../../../../../werewolf-consts/enums";
import {TargetContext} from "../../../../../context/targetContext";
import {SKILL_ALLOWED_IN_STAGE, SKILL_REQUIRE_TARGETS} from "../../../../../werewolf-consts/definitions/SkillCondition";

export interface ISkillDetail {
    skill: SkillNames;
    disabled: boolean;
    reason: string;
}

function useSkills() {
    let targets = useContext(TargetContext).targets;
    let state = useContext(StateContext);
    const {t} = useTranslation(I18Namspace.SkillDisabledReason);
    let roomContext = useContext(RoomContext);
    let roomState = roomContext.room.state;
    const [skills, setSkills] = useState<ISkillDetail[]>([]);
    let myRoleEvents = roomState.players[roomContext.myId]?.role?.roleEvents || [];
    useEffect(() => {
        let skillDetails: ISkillDetail[] = [];
        let unavailableSkills: SkillNames[] = [];
        let availableSkills: SkillNames[] = SKILL_ALLOWED_IN_STAGE[state.currentStage.stageName].filter(
            (s) => s !== SkillNames.POINT
        );
        availableSkills.every((skill) => {
            let disabled = false;
            let reason = "";
            if (SKILL_REQUIRE_TARGETS[skill] && SKILL_REQUIRE_TARGETS[skill] !== targets.length) {
                disabled = true;
                reason = t(`${SkillDisabledReasonEnum.targetCountNotMatch}`, {
                    targetCount: SKILL_REQUIRE_TARGETS[skill]
                });
                skillDetails.push({skill: skill, disabled: disabled, reason: reason});
                unavailableSkills.push(skill);
            }
        });
        switch (state.currentStage.stageName) {
            case StageNames.WAITING_STAGE:
                if (state.roomSetting.roomAdmin !== roomContext.myId) {
                    skillDetails.push({
                        skill: SkillNames.ADMIN_START_GAME,
                        reason: t(`${SkillNames.ADMIN_START_GAME}.${SkillDisabledReasonEnum.notAdmin}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.ADMIN_START_GAME);
                } else if (state.players.size > MAX_PLAYER_IN_GAME) {
                    skillDetails.push({
                        skill: SkillNames.ADMIN_START_GAME,
                        reason: t(`${SkillNames.ADMIN_START_GAME}.${SkillDisabledReasonEnum.tooMuchPlayer}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.ADMIN_START_GAME);
                } else if (state.players.size < MIN_PLAYER_IN_GAME) {
                    skillDetails.push({
                        skill: SkillNames.ADMIN_START_GAME,
                        reason: t(`${SkillNames.ADMIN_START_GAME}.${SkillDisabledReasonEnum.notEnoughPlayer}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.ADMIN_START_GAME);
                }
                break;
            case StageNames.WILD_CHILD:
                if (state.dayNo !== 1) {
                    skillDetails.push({
                        skill: SkillNames.CHOOSE_MOTHER,
                        reason: t(`${SkillDisabledReasonEnum.oneTimeSkill}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.ADMIN_START_GAME);
                }
                break;
            case StageNames.WOLF_PLUS:
                if (countRoleEvent(roomState, roomContext.myId, EventNames.CURSE) > 0) {
                    skillDetails.push({
                        skill: SkillNames.CURSE,
                        reason: t(`${SkillDisabledReasonEnum.oneTimeSkill}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.CURSE);
                }
                break;
            case StageNames.WITCH_SAVE:
                if (countRoleEvent(roomState, roomContext.myId, EventNames.SAVE) > 0) {
                    skillDetails.push({
                        skill: SkillNames.SAVE,
                        reason: t(`${SkillDisabledReasonEnum.oneTimeSkill}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.SAVE);
                }
                break;
            case StageNames.WITCH_KILL:
                if (countRoleEvent(roomState, roomContext.myId, EventNames.KILL) > 0) {
                    skillDetails.push({
                        skill: SkillNames.KILL,
                        reason: t(`${SkillDisabledReasonEnum.oneTimeSkill}`),
                        disabled: true
                    });
                    unavailableSkills.push(SkillNames.KILL);
                }
                break;
            case StageNames.LAST_WORD:
                let deadMans = state.currentStage.deadMans;
                if (deadMans.length === 1 && !deadMans.includes(roomContext.myId)) {
                    let hanged = roomContext.room.state.players[deadMans[0]];
                    if (hanged)
                        skillDetails.push({
                            skill: SkillNames.SKIP,
                            reason: t(`${SkillDisabledReasonEnum.onlyHangedMan}`, {
                                hangedName: `${hanged.name}(${hanged.seat})`
                            }),
                            disabled: true
                        });
                    unavailableSkills.push(SkillNames.SKIP);
                }
                break;
            default:
                break;
        }
        // let skillsInStage = availableSkills;
        for (const skill of availableSkills) {
            if (!unavailableSkills.includes(skill)) {
                skillDetails.push({
                    skill: skill,
                    disabled: false,
                    reason: ""
                });
            }
        }

        setSkills(skillDetails);
    }, [state.currentStage.stageName, state.players, myRoleEvents.length, targets]);
    return skills;
}

export default useSkills;
