import {useContext, useEffect, useState} from "react";
import {SELECT_TYPE} from "../TargetPicker";
import {StateContext} from "../../../../../context/StateContext";
import {RoomContext} from "../../../../../context/room-context";
import {
    MAX_CHOICES_IN_STAGE,
    Stages_Allow_Group_Picker_Type,
    Stages_Allow_Single_Picker_Type,
    Stages_Not_Allow_Last_Targets,
    Stages_Show_ActiveIndicator,
    Stages_Show_Attackers,
    Stages_Show_Dead
} from "../../../StageSettings";

useTargetPickerOptions.propTypes = {};

export interface ITargetOptions {
    targets: string[];
    targetSize?: number;
    showAttacker: boolean;
    showDead: boolean;
    showActiveIndicator?: boolean;
}

interface IPickerOptions {
    targetOptions: ITargetOptions;
    selectType: SELECT_TYPE;
    maxChoices: number;
}

function useTargetPickerOptions() {
    const [options, setOptions] = useState<IPickerOptions>({
        targetOptions: {
            showActiveIndicator: true,
            showAttacker: true,
            showDead: true,
            targets: []
        },
        selectType: SELECT_TYPE.SINGLE,
        maxChoices: 1
    });
    let stateContext = useContext(StateContext);
    let myID = useContext(RoomContext).myId;
    useEffect(() => {
        const currentStage = stateContext.currentStage.stageName;
        let listPlayers = [...stateContext.players.keys()];
        if (Stages_Not_Allow_Last_Targets.includes(currentStage)) {
            let lastTargets: string[] = stateContext.players[myID]?.role.roleData.lastTarget || [];
            listPlayers = listPlayers.filter((playerID) => {
                return !lastTargets.includes(playerID);
            });
        }
        const newOptions = {} as IPickerOptions;
        newOptions.targetOptions = {
            targets: listPlayers,
            showAttacker: Stages_Show_Attackers.includes(currentStage),
            showDead: Stages_Show_Dead.includes(currentStage),
            showActiveIndicator: Stages_Show_ActiveIndicator.includes(currentStage)
        };
        newOptions.maxChoices = MAX_CHOICES_IN_STAGE[currentStage];
        if (Stages_Allow_Single_Picker_Type.includes(currentStage)) {
            newOptions.selectType = SELECT_TYPE.SINGLE;
        } else if (Stages_Allow_Group_Picker_Type.includes(currentStage)) {
            newOptions.selectType = SELECT_TYPE.GROUP;
        } else newOptions.selectType = SELECT_TYPE.NONE;
        setOptions(newOptions);
    }, [myID, stateContext.currentStage.stageName, stateContext.players]);
    return options;
}

export default useTargetPickerOptions;
